.nav{
    width: 100%;
    display: flex;
    justify-content: flex-end;
}



.nav ul{
    list-style-type: none;
    display: flex;
    justify-content: space-around;
    font-family: 'BebasNeue';
    padding: 0;
    margin: 10px;
    font-size: 20px;
}

.nav li{
    color: #0f5352;
    margin: 10px;
    text-shadow: 0 0;
}

.nav li:hover{
    color: #167c7b;
}

a.active{
    color: #e8e9f2;
}

.nav li a{
    text-decoration: none;
}