@font-face {
  font-family: 'BebasNeue';
  src: local('BebasNeue'), url(./fonts/BebasNeue-Regular.ttf) format('truetype');
}

@font-face {
  font-family: 'Poppins';
  src: local('Poppins'), url(./fonts/Poppins-Regular.otf) format('opentype');
}

@font-face {
  font-family: 'PoppinsL';
  src: local('PoppinsL'), url(./fonts/Poppins-Light.otf) format('opentype');
}

*{
  margin: 0;
}

.app{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background: #1A2980;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #26D0CE, #1A2980);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #26D0CE, #1A2980); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  min-height: 100vh;

}

.app__content{
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 800px;
  height: 750px;
  background: rgba(255, 255, 255, 0.20);
  /* box-shadow: 0 8px 32px 0 rgba(255, 255, 255, 0.37); */
  backdrop-filter: blur( 11.0px );
  -webkit-backdrop-filter: blur( 11.0px );
  border-radius: 10px;

}

a, a:visited{
  color: inherit;
}

p{
  color: #072929;
  font-family: 'BebasNeue';
  font-size: 1.5rem;
}



h2{
  font-size: 48px;
  letter-spacing: 5px;
  color: #e8e9f2;
  font-family: "BebasNeue";
  text-align: center;
}

h3{
  font-family: "BebasNeue";
  color: #0a1033;
  font-size: 36px;

}

@media(max-width:1024px){
  .app__content{
    height: 100vh;
    width: 100%;
    border-radius: 0;
  }
}

@media(max-width:500px){


  h2{
    font-size: 36px;
  }

  h3{
    font-size: 30px;
  }

  p{
    font-size: 1.3rem;
  }
}
