.about{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
    flex-grow: 1;
    overflow: scroll;
    padding: 0 10px;
}

.about::-webkit-scrollbar{
    display: none;
}

.about h2{
    margin-top: 0.5em;
}


.about h3{
    margin-top: 3em;
}

.about__left__container{
    padding: 1px;
    background: rgb(231, 231, 231);
    border-radius: 10px;
  
}

@media(max-width: 500px){
    .about{
        margin-top: 0;
    }

    .about p{
        text-align: center;
    }
}