.projects{
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-grow: 1;
    overflow: scroll;
    margin: 10px 0;
}

.projects::-webkit-scrollbar{
    display: none;
}

.projects__item{
    margin-top: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 90%;
    min-height: 200px;
    background: rgba(255, 255, 255, 0);
    backdrop-filter: blur( 11.0px );
    -webkit-backdrop-filter: blur( 11.0px );
    border-radius: 10px;
    padding: 15px;
    transform: translateX(0);
  
}

.projects__item p{
    font-family: 'PoppinsL';
    font-size: 1.1em;
    margin-bottom: 1em;
}

.projects__item__right{
    width: 50%;
}

.projects__item__right div{
    justify-content: flex-end;
}


.projects__item__right a{
    text-decoration: none;
    font-family: "BebasNeue";
    color: #dfdede;
    font-size: 20px;
    margin-right: 1.5em;
    text-shadow: 0 0;
}

.projects__item img{
    max-width: 280px;
    max-height: 200px;
}

@media(max-width:650px){
    .projects__item{
        flex-direction: column;
        min-height: unset;
    }

    .projects__item__right{
        text-align: center;
        width: 80%;
    }

    .projects__item__right a{
        margin: 0 .5rem;
    }

    .projects__item p{
        font-size: 1em;
    }
}

@media(max-width:300px){
    .projects__item img{
        max-width: 250px;
    }
}