.skills{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 480px;
}


.skills__items{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 1rem;
}

.skills a{
    display: block;
    margin:10px;
}

@media(max-width:500px){
    .skills{
        width: 80vw;
    }
}
